import React from 'react';
import Temp from './Temp';

import style from './Card.module.css';

export default function Card({max, min, name, img, onClose, main}) {
  // acá va tu código
  return (
  <div className={[style.card, main ? style.mainCard : ''].join(' ')}> {/* main ? is equal to main === {true}*/}   
    <span className={style.name}>{name}</span>
    <button onClose={onClose} className={style.cardBtn}>X</button>
    <section>
      <Temp label="Max" value={max}/>
      <Temp label="Min" value={min}/>
      <img src={`http://openweathermap.org/img/wn/${img}@2x.png`} alt="icono" />
    </section>
  </div>
  );
};